@import url(https://fonts.googleapis.com/css?family=Russo+One&display=swap&subset=cyrillic,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Odibee+Sans&display=swap);
*{margin:0;padding:0;font-family:'Russo One', sans-serif;color:rgba(0,0,0,0.842)}body{background-position:0 0;background-position:initial;background-repeat:repeat;background-size:auto auto;background-size:initial;min-height:100vh;position:relative;background:url(../../static/media/background.8bc907b7.jpg);overflow:auto}.app{width:70vw;max-width:800px;min-width:300px;padding:15px;position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;border-radius:20px;box-shadow:14px 20px 27px 17px rgba(0,0,0,0.54);background:radial-gradient(circle, #fff 0%, rgba(255,255,255,0.74) 100%)}.searchBox{width:100%;position:relative}.searchBox #searchTable{-webkit-transform:translate(0, -7px);transform:translate(0, -7px);left:170px;width:calc(100% - 176px);position:absolute;border-bottom:3px solid rgba(0,0,0,0.26);border-left:3px solid rgba(0,0,0,0.26);border-right:3px solid rgba(0,0,0,0.26);background:#fff;display:none}.searchBox #searchTable tbody{display:flex;flex-direction:column}.searchBox #searchTable tbody,.searchBox #searchTable th,.searchBox #searchTable tr,.searchBox #searchTable button{width:100%}.searchBox #searchTable th{display:block}.searchBox #searchTable button{background:transparent;border-top:3px solid rgba(0,0,0,0.26);border-bottom:none;border-left:none;border-right:none;border-radius:0;border-radius:initial}.searchBox #searchTable button:hover{background:rgba(248,0,0,0.644)}.searchBox form{width:-webkit-fill-available;display:flex}.searchBox #btn0,.searchBox #btn1,.searchBox #btn2,.searchBox #btn3,.searchBox #btn4{display:none}.searchBox button{font-size:20px;padding:10px 20px;border-radius:10px;background:rgba(255,0,0,0.397);border:3px solid rgba(0,0,0,0.26);cursor:pointer;position:relative}.searchBox button .searchBtn{width:45px;height:25px;display:inline-block;margin:0 0 0 20px;-webkit-transform:translate(0, 5px);transform:translate(0, 5px)}.searchBox button:hover{background:rgba(255,0,0,0.534)}.searchBox .searchBar{width:-webkit-fill-available;width:-moz-available;background:rgba(255,255,255,0.212);border-radius:10px;border:3px solid rgba(0,0,0,0.26);color:#6d6d6d;font-size:20px;padding:10px 20px;-webkit-appearance:none;-moz-appearance:none;appearance:none;outline:none;text-align:center;caret-color:#3a3a3a}.searchBox .searchBar::-webkit-input-placeholder{color:#000}.searchBox .searchBar:-ms-input-placeholder{color:#000}.searchBox .searchBar::-ms-input-placeholder{color:#000}.searchBox .searchBar::placeholder{color:#000}.searchBox .searchBar:hover{background:rgba(255,255,255,0.76)}.searchBox .searchBar:focus{background:#fff;color:#000}.searchBox .searchBar:focus::-webkit-input-placeholder{color:rgba(17,17,17,0.781)}.searchBox .searchBar:focus:-ms-input-placeholder{color:rgba(17,17,17,0.781)}.searchBox .searchBar:focus::-ms-input-placeholder{color:rgba(17,17,17,0.781)}.searchBox .searchBar:focus::placeholder{color:rgba(17,17,17,0.781)}.mainInfo{padding:15px;margin:25px 0 0 0;font-size:20px;display:none;-webkit-transition:opacity 500ms;transition:opacity 500ms}.mainInfo img{width:40px;height:22px;margin:0 15px}.mainInfo h2{display:inline-block}.mainInfo .temp{display:flex;justify-content:center;margin-top:50px}.mainInfo .temp i{color:#000;font-size:150px;margin:0 0 0 25px}.mainInfo .temp h1{font-size:150px;font-family:'Odibee Sans', cursive;color:#000}.mainInfo .additional{margin:20px 0 -20px 0}.mainInfo .additional h3{margin:10px 0;border-bottom:4px solid #d0d0d0}@media screen and (max-width: 700px){.app{width:85vw;height:auto}.app{min-width:250px}.searchBox #searchTable{-webkit-transform:translate(0, -8px);transform:translate(0, -8px);left:0;width:99%;position:absolute}.searchBox #searchTable th{display:block}.searchBox #searchTable button{margin:0;padding:8px 0}.searchBox form{display:block}.searchBox button{margin-top:5px;padding:5px 20px;width:100%}.mainInfo{padding:10px;margin:15px 0 0 0;font-size:15px}.mainInfo img{height:22px;margin:0 0 -2px 15px}.mainInfo .temp{display:block;margin:10px auto}.mainInfo .temp i{font-size:100px;margin:10px auto 0 auto}.mainInfo .temp h1{font-size:150px}}

